
body {
  margin: 0;
  padding: 0;
  background-color: #050e1f;
  background-image: 
    linear-gradient(to bottom, rgba(5, 14, 31, 0) 70%, rgba(5, 14, 31, 1)), /* Transparent to background color */
    url('./assets/bg_image.jpg'); /* Background image */
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
  height: 100vh;
}

.logo {
  background-image: url('./assets/logo.png');
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* NavBar style */
.navbar {
  background-color: #050e1f;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 5px;
  display: flex;
  align-items: center;
  height: 30px; /* Adjust the height as needed */
}

.nav-links {
  /* Remove display: none; to always display the menu items */
  display: flex;
  align-items: center;
}

.nav-links.active {
  display: flex; /* Show the menu items when active */
}

.nav-links a {
  color: #fac34d; /* Set the color to white or any other desired color */
  padding: 10px 20px; /* Add some padding to the links */
  text-decoration: none; /* Remove underline */
}

.nav-links a:hover {
  /* Optionally, define styles for when the link is hovered */
  text-decoration: underline; /* Add underline on hover if desired */
}

.menu-icon {
  display: none; /* Hide the menu icon by default */
  cursor: pointer;
}
  
.line {
  width: 30px;
  height: 3px;
  background-color: #fac34d;
  margin: 5px 0;
}
  
@media screen and (max-width: 768px) {
  .menu-icon {
    display: block; /* Show the menu icon when screen size is less than or equal to 768px */
    position: absolute;
    top: 5px;
    right: 10px; /* Adjust the distance from the right side of the screen */
  }

  .nav-links {
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 30px;
    right: 10px;
    background-color: #050e1f;
    opacity: 0.9;
    width: fit-content;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    display: none;
  }

  .nav-links.active {
    display: flex;
  }

  .nav-links a {
    width: 100%;
    text-align: center;
  }
}

.container {
  margin: 2%;
  /*
  margin-top: 20px;
  margin-left: 10px;
  margin-right: 10px;*/
  background-color: #050e1f;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  border-radius: 10px;
}
.container p {
  color: #ffffff;
  font-size: 20px;
  margin: 0;
  padding: 0;
}
.container h1 {
  color: #ffffff;
  margin-top: 15px;
  padding: 5px;
  padding-top: 15px;
}
